/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Monkeylars","date":"20 Aug 2001","slug":"/2001-08-20-monkeylars/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/08/fphcrh.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"fphcrh.jpg: \" /></p>\n<p>My grandfather used to call us kids \"Monkeylars\" when we were causing too much trouble or making too much noise. For the same reason I've taken to calling Christopher Monkeylars. A search on Google reveals that the word <a hrehttp://www.google.com/search?q=monkeylars>Monkeylars does not exist yet on the web. Well, it does now.</p>"},"next":{"fields":{"title":"Self Serve","date":"18 Aug 2001","slug":"/2001-08-18-self-serve/"}},"previous":{"fields":{"title":"Back On Location","date":"21 Aug 2001","slug":"/2001-08-21-back-on-location/"}}},{"node":{"fields":{"title":"Self Serve","date":"18 Aug 2001","slug":"/2001-08-18-self-serve/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/08/crhjelly.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"crhjelly.jpg: \" /></p>\n<p>Christopher decided to serve his own breakfast this morning. Brötchen with strawberry jelly, but please hold the Brötchen. More pictures from yesterday and today are <a href=\"http://www.shcon.com/index.php?album=08_2001%2F20010818&dispsize=512&start=0\">here</a>. (The Pictures link above has been updated to point to the new pictures site.)</p>\n<p>Next week Mama travels to Verona for a meeting with the exective board and a visit to the <a href=\"http://194.149.233.39/English/\">opera</a> on Friday night. (Spouses were also invited, but we felt that Christopher is too young yet to leave with a babysitter for 4 days, so the men are staying home.) But on Saturday afternoon, her brother and his wife are celebrating their 10th anniversary with a <a href=\"http://www.ms-stadt-kiel.de/\">chartered cruise</a> in the Kiel Bay. And looking at the flight plans, there is no way to get from Verona to Kiel in time for the cruise. Not unless she charters a helicopter. I don't suppose <a href=\"http://www.curry.com/\">Adam Curry</a> will loan us his.</p>\n<p>Travel plans: We'll be in Minnesota for 3 weeks in December to celebrate Christmas and Christopher's second birthday, returning before New Year's. Next spring, Mama has a meeting in Las Vegas the last week of April. Christopher and I will tag along, and the authorities in Vegas have been <a href=\"http://hal.editthispage.com/discuss/msgReader$901\">warned</a>. We'll probably also swing through the West Coast, so that Mama won't be able to complain anymore that I've never taken her to San Francisco.</p>"},"next":{"fields":{"title":"Day Off","date":"17 Aug 2001","slug":"/2001-08-17-day-off/"}},"previous":{"fields":{"title":"Monkeylars","date":"20 Aug 2001","slug":"/2001-08-20-monkeylars/"}}},{"node":{"fields":{"title":"Day Off","date":"17 Aug 2001","slug":"/2001-08-17-day-off/","tags":"Y2001"},"html":"<p>Mama and Christopher are off to Oma Ruth's today, to give her some help for her move at the end of September. She'll then be in Buchholz,  5 minutes away instead of 90 minutes. Anyway, on my last vacation day I'm bach'ing it. (That's 'Strohwitwe' for the German audience.)</p>\n<p>The test site <a href=\"http://www.papascott.de\">papascott.de</a> got <a href=\"http://www.dangerousmeta.com/posts/01/20010816\">dang-meta'ed</a>. I've started putting up some real content so that the folks don't get too bored.</p>"},"next":{"fields":{"title":"Sun Is Shining","date":"16 Aug 2001","slug":"/2001-08-16-sun-is-shining/"}},"previous":{"fields":{"title":"Self Serve","date":"18 Aug 2001","slug":"/2001-08-18-self-serve/"}}}],"pathPrefix":"","first":false,"last":false,"index":983,"pageCount":1101}}

/***/ })

